.wrapper {
  margin-bottom: 1rem;
}
.input {
  border: 1px solid #eee;
  display: block;
  width: 100%;
  font-size: 1rem;
  padding: 0.8rem;
  border-radius: .4rem;
  background: #eee;
  transition: .3s;
}
.input:focus,
.input:hover{
  outline: none;
  border-color: #fb1;
  background: white;
  box-shadow: 0 0 0 2px #fea;
}
.label{
display: block;
font-size: 1rem;
line-height: 1.4;
padding-bottom: .1rem;
}
.error{
  color: #f31;
  font-size: .875rem;
  margin-top: .25rem;
}